import React, { useState } from 'react';

const Ex2 = () => {
  const [password,setPassword] =useState('')
  // const [message,setMessage] = useState()
  function handlChangePassword(e){
setPassword(e.target.value)
  }
  return (
    <div>
      <h2>Exercice 2:</h2>
Entrer votre password : <input type="text" onChange={handlChangePassword} value={password} />
<h3>{password.length<4?'Password doit avoir au moin t 4 caracteres':''}</h3>
      <hr /><hr />
    </div>
  );
}

export default Ex2;
