import React from 'react'
 class Ex3 extends React.Component{
  constructor(props){
    super(props);
    this.state={
      comptuer:0,
      
    }
  }
 Augmenter=()=>this.setState({comptuer:this.state.comptuer+1})

 Diminuer(){
 return this.setState({comptuer:this.state.comptuer-1})
}




  render(){
    return (
  <div>
        <h2>Exercice 3:</h2>
  
  <h1>Compteur :{this.state.comptuer} </h1>
  
  <button onClick={this.Augmenter}>Augmementer</button>
  <button onClick={()=>this.Diminuer()}>Diminuer</button>
  
          <hr /><hr />
  </div>
    )
  }
 }

 export default Ex3;