import React, {Component} from 'react'
class Ex2 extends Component {
  constructor (props) {
    super(props);
    this.state={
      password:""
    }
  }
  render() {
    return (
      <div>
        <h2>Exercice 2:</h2>
    <input type="text" onChange={(e)=>this.setState({password:e.target.value})}/> <br />
    {this.state.password.length<4?"Password doit avoir au moins t 4 caracteres ":''}
    
        <hr /><hr />
      </div>
    );
  }
}
export default Ex2;
