import React,{Component} from 'react'

class Ex4 extends Component {
  constructor(props){
    super(props)
    this.state={
      a:0,
      b:0,
      oper:'+',
      res:0
    }
  }


  Calcule = (op)=>{
    if(op==='+'){
      this.setState({res:parseFloat(this.state.a)+parseFloat(this.state.b)})
    }
    else if(op==='-'){
      this.setState({res:this.state.a-this.state.b})
    }else if(op==='*'){
      this.setState({res:this.state.a*this.state.b})
    }else if(op==='/'){
      this.setState({res:this.state.a/this.state.b})
    }
  }

  render(){
    return(
      <div>
      <h2>Exercice 4:</h2>
<input type="text" onChange={(e)=>(this.setState({a:e.target.value}))}/>
<select onChange={(e)=>this.setState({oper:e.target.value})}>
  <option value="+">+</option>
  <option value="-">-</option>
  <option value="*">*</option>
  <option value="/">/</option>
</select>
<input type="text" onChange={(e)=>(this.setState({b:e.target.value}))}/>
<button onClick={()=>this.Calcule(this.state.oper)} >=</button>
<p>
resultat : {this.state.res}
</p>

        <hr /><hr />
</div>
    )
  }
}


export default Ex4;