import React, { useState } from 'react';

const Ex6 = () => {
  const [nom,setNom]=useState()
  const [email,setEmail]=useState()
  const [message,setMessage]=useState()
  const [res,setRes]=useState()
  function Soumettre(){
    setRes(
      <>
      <h1>Countenu du formulaire soumis :</h1>
      Nom : {nom} <br />
      E-mail: {email} <br />
      Message : {message} <br />
      </>
    )
    
  }
    return (
    <div>
      <h2>Exercice 6:</h2>
  Nom: <input type="text" onChange={(e)=>setNom(e.target.value)}/><br />
  E-mail: <input type="text" onChange={(e)=>setEmail(e.target.value)}/><br />
  Message: <textarea type="text" onChange={(e)=>setMessage(e.target.value)}/><br />
  <button onClick={()=>Soumettre()}>Soumettre</button>
  {res}
      <hr /><hr />
    </div>
  );
}

export default Ex6;
