import React, {Component} from 'react'

class Ex5 extends Component {

constructor(props) {
  super(props)
  this.state={
tache:'',
listeTaches:[]

  }
}

AjouterTache=()=>{
this.setState({listeTaches:[...this.state.listeTaches,this.state.tache]})
}

Sepprimer=(i)=>{
  this.setState({listeTaches:[...this.state.listeTaches.filter(tache=>tache!=i)]})
}
  render(){
    return (
      <div>
        <h2>Exercice 5:</h2>
<input type="text"  placeholder='Ajouter une nouvelle tache' onChange={(e)=>this.setState({tache:e.target.value})}/> <button onClick={()=>this.AjouterTache()}>Ajouter</button>
<ul>
  {this.state.listeTaches.map((tache,i)=><li key={i}>{tache} <button onClick={()=>this.Sepprimer(tache)}>Supprimer</button></li>)}
</ul>
        <hr /><hr />
      </div>
    )
  }
}

export default Ex5;