import React, { useState } from 'react';

const Ex4 = () => {
  const [a,setA]=useState()
  const [b,setB]=useState()
  const [oper,setOper]=useState('+')
  const [res,setRes]=useState()
  
function Calcule(){
switch(oper){
  case '+': setRes(a+b); break;
  case '-': setRes(a-b); break;
  case '*': setRes(a*b); break;
  case '/': setRes(a/b); break;
  // default : setRes(res)
}
}
  return (
    <div>
      <h2>Exercice 4:</h2>
<input type="text" onChange={(e)=>setA(parseFloat(e.target.value))}/>
<select onChange={(e)=>setOper(e.target.value)}>
  {/* <option value="">oper</option> */}
  <option value="+" >+</option>
  <option value="-">-</option>
  <option value="*">*</option>
  <option value="/">/</option>
</select>
<input type="text" onChange={(e)=>setB(parseFloat(e.target.value))}/>
<button onClick={()=>Calcule()}>=</button>
<h1>Resultat: {res}</h1>
      <hr /><hr />
    </div>
  );
}

export default Ex4;
