import React, { Component } from 'react';

class Ex1 extends Component {
  constructor(props){
    super(props);
    this.state={
      nom:'',
      penom:'',
      message:''
        }
  }
  render() {
    return (
      <div>
        <h2>Exercice 1: </h2>
      Nom: <input type="text" onChange={(e)=>this.setState({nom:e.target.value})}/> <br />
      Prenom: <input type="text" onChange={(e)=>this.setState({penom:e.target.value})}/> <br />
      <button onClick={()=>this.setState({message:`Message : ${this.state.nom} ${this.state.penom}`})}>Afiicher</button>
      <h3>{this.state.message}</h3>
      <hr /><hr />
      </div>
    );
  }
}

export default Ex1;
