import React, { useState } from 'react';

const Ex1 = () => {
  const [nom,setNom] = useState()
  const [prenom,setPrenom] = useState()
  const [message,setMessage]=useState()
function handelNom(e){
  setNom(e.target.value)
  
}
  return (
    <div>
      <h2>Exercice 1:</h2>
      nom:
      <input type="text" onChange={handelNom}/>
      <br />
      prenom:
      <input type="text" onChange={(e)=>setPrenom(e.target.value)}/><br />
      <button onClick={()=>setMessage(`Message: ${nom} ${prenom}`)}>Afficher</button>
      <h3>{message}</h3>
      <hr />
      <hr />
    </div>
  );
}

export default Ex1;
