import React, { Component } from 'react';

class Ex6 extends Component {
  constructor(props) {
    super(props);
    this.state={
      nom:'ss',
      email:'',
      message:'',
      res:false
    }
  }
  render() {
    return (
      <div>
        <h2>Exercice 6:</h2>
<table>
  <tr>
    <th>
      Nom: 
    </th>
    <td><input type="text" onChange={(e)=>this.setState({nom:e.target.value})}/></td>
  </tr>
  <tr>
    <th>
      E-mail: 
    </th>
    <td><input type="text" onChange={(e)=>this.setState({email:e.target.value})}/></td>
  </tr>
  <tr>
    <th>
      Message: 
    </th>
    <td><textarea type="text" onChange={(e)=>this.setState({message:e.target.value})} /></td>
  </tr>
  <tr>
    <td>
      <button onClick={()=>this.setState({res:true})}>Soumettre</button>
    </td>
  </tr>
</table>
{this.state.res==true?<section>
  <h3>Contenu du formulaire soumis :</h3>
  <p>Nom : {this.state.nom}</p>
  <p>E-mail: {this.state.email}</p>
  <p>Message: {this.state.message}</p>
</section>:''}
        <hr /><hr />
      </div>
    );
  }
}

export default Ex6;
