
import axios from 'axios';
import React, { useEffect, useState } from 'react';

const Commopnont1 = () => {
const [filmes,setFilmes]=useState('');
const [search,setSearch]=useState("avatar")
// const rrr={id:1,nom:"abdo"};

  useEffect(()=>{
    axios.get(`https://www.omdbapi.com/?s=${search}&apikey=f5c64401`)
    .then(response=> setFilmes(response.data))
    
    .catch(error=>console.error('Erreur axios: ',error))
  },[search]);

  return (
    <div>
      <section>
        <label htmlFor="serche">Search:</label> <input type="text"  id='serche' onChange={(e)=>setSearch(e.target.value)}/> 
        {/* <button onClick={()=>Search()}>Search</button> */}
        </section>
        {( filmes.Response ==='True')?(
      <table border={'1px'}>
        <tr>
          <th>Titel</th>
          <th>Year</th>
          <th>Porster</th>
        </tr>
    
      {filmes.Search.map((element,index)=><tr key={index}>
        <td>{element.Title}</td>
        <td>{element.Year}</td>
        <td><img src={`${element.Poster}`} alt=""  style={{width:"200px",height:"150px"}}/></td>

      </tr>)}
      </table>):"aucun"}

    </div>
  );
}

export default Commopnont1;
