import React, { useState } from 'react';

const Ex5 = () => {
  const [tache,setTache]=useState()
  const [taches,setTaches]=useState([])
  function Supprimer(tachei){
    setTaches([...taches.filter(tache => tache!=tachei)])
  }
  return (
    <div>
      <h2>Exercice 5:</h2>
<input type="text" placeholder='Ajouter une nouvelle tache' onChange={(e)=>setTache(e.target.value)}/> <button onClick={()=>setTaches([...taches,tache])}>Ajouter</button>
<ul>
  {taches.map((tache, i)=><li key={i}>{tache} <button onClick={()=>Supprimer(tache)}>Supprimer</button></li>)}
</ul>
      <hr /><hr />
    </div>
  );
}

export default Ex5;
